@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800&display=swap');
@import url('https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;500;700&display=swap');
@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
	html {
		font-family: 'DM Sans', sans-serif !important;
		font-feature-settings: 'kern' !important;
		-webkit-font-smoothing: antialiased;
		letter-spacing: -0.5px;
	}
}

input.defaultCheckbox::before {
	content: url(../src/assets/svg/checked.svg); 
	color: white;
	opacity: 0; 
	height: 16px; 
	width: 16px;
	position: absolute; 
	left: 50%;
	transform: translate(-50%, 0px);
} 

input:checked.defaultCheckbox::before { 
	opacity: 1;
}


.material-symbols-outlined {
  font-variation-settings:
  'FILL' 0,
  'wght' 200,
  'GRAD' 0,
  'opsz' 24
}

/** CSS For waves */

.waves-container {
	position: fixed;
	bottom: 0;
	width: 100%;
	max-height: 50px;
	overflow: hidden;
	z-index: 1;
  }
  
  .waves {
	position: relative;
	width: 100%;
	margin-bottom: -10px; /*Fix for safari gap*/
	min-height: 50px;
	max-height: 100px;
  }
  
  .content {
	position: relative;
	height: 10vh;
	text-align: center;
	background-color: #5143d9;
  }
  
  /* Animation */
  
  .parallax > use {
	animation: move-forever 25s cubic-bezier(0.55, 0.5, 0.45, 0.5) infinite;
  }
  .parallax > use:nth-child(1) {
	animation-delay: -1s;
	animation-duration: 3s;
  }
  .parallax > use:nth-child(2) {
	animation-delay: -2s;
	animation-duration: 5s;
  }
  .parallax > use:nth-child(3) {
	animation-delay: -3s;
	animation-duration: 8s;
  }
  .parallax > use:nth-child(4) {
	animation-delay: -4s;
	animation-duration: 10s;
  }
  @keyframes move-forever {
	0% {
	  transform: translate3d(-90px, 0, 0);
	}
	100% {
	  transform: translate3d(85px, 0, 0);
	}
  }
  /*Shrinking for mobile*/
  @media (max-width: 768px) {
	.waves {
	  height: 40px;
	  min-height: 40px;
	}
	.content {
	  height: 30vh;
	}
	h1 {
	  font-size: 24px;
	}
  }
  
  /** mic CSS */
  .mic {
	width: 80px;
	height: 80px;
	background: black;
	border-radius: 80px;
	display: flex;
	justify-content: center;
	align-items: center;
	position: fixed;
	z-index: 99;
	bottom: 8px;
	right: 380px;
	margin-left: -40px;
	border: white solid 2px;
	transition: all 0.2s ease-in-out;
	&:hover {
	  width: 84px;
	  height: 84px;
	  margin-left: -40px;
	  bottom: 6px;
	  background: #5143d9;
	}
	&.active {
	  // background: #5143d9;
	}
	cursor: pointer;
	span.material-symbols-outlined {
	  font-size: 48px;
	  color: white;
	}
  }
  
  .transcript {
	position: fixed;
	right: 420px;
	margin-right: 55px;
	bottom: 10px;
	background: #18204d;
	color: white;
	border-radius: 10px;
	padding: 20px 30px;
	font-size: 24px;
	border: #5143d9 solid 2px;
	z-index: 2;
	.arrow {
	  content: "";
	  position: absolute;
	  border-left: 15px solid #18204d;
	  border-top: 15px solid transparent;
	  border-bottom: 15px solid transparent;
	  right: -15px;
	  margin-top: 3px;
	}
  }
  
  .info-message{
	position: fixed;
	display:flex;
	align-items: center;
	color: white;
	bottom: 0;
	font-size: small;
	padding:10px 20px;
	border-radius: 5px 5px 0 0;
	left: 50%;
	transform: translate(-50%, 0);
	z-index: 9;
  }
  
  .info-message .material-symbols-outlined{
	&:hover{
	  transform: scale(1.1);
	  opacity:0.7;
	}
  }
  
  .tips{
	&:hover .tips-cntr{
	  display:block;
	}
  }
  
  
  .tips-cntr{
	display:none;
	position: fixed;
	align-items: center;
	color: white;
	bottom: 60px;
	padding:10px 20px;
	border-radius: 5px 5px 0 0;
	min-width: max-content;
  }
  
  .cursor-pointer {
	cursor: pointer;
  }
  
  /*Mic ripples*/
  .circles {
	height: 180px;
	width: 180px;
	position: fixed;
	z-index: 1;
	bottom: -40px;
	right: 332px;
	margin-left: -90px;
	> div {
	  animation: growAndFade 3s infinite ease-out;
	  background-color: #5143d9;
	  border-radius: 50%;
	  height: 100%;
	  opacity: 0;
	  position: absolute;
	  width: 100%;
	}
  
	.circle1 {
	  animation-delay: 0.2s;
	}
	.circle2 {
	  animation-delay: 1s;
	}
	.circle3 {
	  animation-delay: 2s;
	}
  }
  
  @keyframes growAndFade {
	0% {
	  opacity: 0.25;
	  transform: scale(0.5);
	}
	100% {
	  opacity: 0;
	  transform: scale(1);
	}
  }
